<template>
  <div class="homeSort">
    <ul class="parentUl">
      <li v-for="(item,index) of list">
        <a @click="clickItem(index)">{{item.name}}</a>
        <ul class="childUl" v-if="index===focusIndex">
          <li v-for="childItem of item.list">
            <router-link :to="{name:'ListById',params: {dishId:childItem.id}}" >{{childItem.name}}</router-link>
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'HomeSort',
    data () {
      return {
        list: [],
        focusIndex: '',
      }
    },
    methods :{
      clickItem:function (index) {
        if(index === this.focusIndex){
          this.focusIndex = '';
        }else{
          this.focusIndex = index;
        }
      }
    },
    mounted: function () {
      this.$http.post('/api/category',
        {
          key: '5c1abcdb494382186bf2ea20a6aff5a4'
        },{
          emulateJSON:true
        }).then(function(response){

        this.list = JSON.parse(response.bodyText).result;

      },function(err){
        console.log(err)
      });
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .parentUl{
    width:100%;
  }
  .parentUl>li>a{
    display: block;
    height: 50px;
    line-height: 50px;
    text-align: center;
    color: #fff;
    background-color: #29B4F0;
  }
  .childUl>li{
    text-align: center;
    border-bottom: 1px solid #404040;
  }
</style>



