
<template>
  <div class="homeSearch" :style="{backgroundImage:'url('+bgImg+')'}">
    <div class="box">
      <input type="text" v-model="value" class="input" placeholder="发现美食">
      <router-link class="fa fa-search search" :to="{ name: 'ListByName', params: {dishName:value}}"></router-link>
    </div>
  </div>
</template>

<script>
  let bgArray = [require('../assets/searchBg_01.jpg'), require('../assets/searchBg_02.jpg'), require('../assets/searchBg_03.jpg')]
  export default {
    name: 'HomeSearch',
    data () {
      return {
        bgImg: bgArray[0],
        value: ''
      }
    },
    created: function () {
      //从数组随机去一个当背景图片
      this.bgImg = bgArray[Math.floor(Math.random() * bgArray.length)]
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .homeSearch{
    flex-grow: 1;
    display: flex;
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
  }
  .box{
    display: flex;
    flex-grow: 1;
    height: 50px;
    line-height: 50px;
    margin: 100px 20px;
  }
  input{
    border: none;
    padding: 0 10px;
    outline: none;
  }
  .input{
    flex-grow: 5;
    background-color: #fff;
  }
  .search{
    flex-grow: 1;
    background-color: darkgrey;
    text-align: center;
    color: #fff;
    font-size: 30px;
    line-height: 50px;
  }
</style>



