<template>
  <div class="list">
    <my-nav></my-nav>
    <ul>
      <li v-for="item of list">
        <router-link :to="{name: 'Detail',params: {id :item.id}}">{{item.title}}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
  import httpRequest from '../network'
  import ajax from '../network/ajax'
  import $ from 'jquery'
  export default {
    name: 'List',
    data () {
      return {
        list: []
      }
    },
    mounted: function () {
      let {dishName,dishId} = this.$route.params;
//      ajax({
//        method: 'GET',
//        url: '/api/nba',
//        data: {
//          key: '54b40fdd27e818b2bff7c58538a5b687'
//        },
//        success: function (response) {
//          console.log(response)
//        }
//      })

//      httpRequest('/api/nba', 'POST', {}, (data) => {
//        console.log(data)
//      })
      let url,params = {
        key: '5c1abcdb494382186bf2ea20a6aff5a4'
      }

      if(dishName){
        params.menu = dishName;
        url = '/api/query.php';
      }else{
        params.cid = dishId;
        url = '/api/index';
      }
      this.$http.post(url, params,
        {
          emulateJSON:true
        }).then(function(response){
          console.log(response)
        this.list = JSON.parse(response.bodyText).result.data;

      },function(err){
        console.log(err)
      });


//      $.get('/api/nba',{key:'54b40fdd27e818b2bff7c58538a5b687'},function(data){
//        console.log(data);
//      },"json")
    },
    destroyed: function () {
      console.log('list销毁了')
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  li{
    height: 50px;
    line-height: 50px;
    text-align: center;
  }
</style>



