<template>
  <div class="nav">
    <div class="btn"  @click="goBack()">
      <i class="fa fa-chevron-left fa-lg"></i>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Nav',
    data () {
      return {
      }
    },
    methods: {
      goBack: function(){
        this.$router.go(-1);
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .nav{
    height: 40px;
    background-color: #ffffff;
  }
  .btn{
    height:40px;
    width:40px;
    line-height: 40px;
    text-align: center;
  }
</style>



