<template>
  <div class="detail">
    <my-nav></my-nav>
    <h1>{{detailData.title}}</h1>
     <p>{{detailData.tags}}</p>
    <p>{{detailData.imtro}}</p>
    <p>{{detailData.ingredients}}</p>
    <p>{{detailData.burden}}</p>
    <img :src="detailData.albums" alt="">
    <ul>
      <li v-for="item of detailData.steps">
        <img :src="item.img" alt="">
        <p>{{item.step}}</p>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'Detail',
    data () {
      return {
        detailData: {
          id:"1",
          title:"",
          tags:"",
          imtro:"",
          ingredients:"",
          burden:"",
          albums:[],
          steps:[
            {
              img:"",
              step:""
            },
          ]
        }
      }
    },
    mounted: function () {
      let id = this.$route.params.id;
      let params = {
        key: '5c1abcdb494382186bf2ea20a6aff5a4',
        id
      }
      this.$http.post('/api/queryid', params,
        {
          emulateJSON:true
        }).then(function(response){
        console.log(JSON.parse(response.bodyText).result.data)
        this.detailData = JSON.parse(response.bodyText).result.data[0];

      },function(err){
        console.log(err)
      });
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



