<template>
  <div class="home">
    <header>{{title}}</header>
    <section>
      <HomeSearch  v-bind:style="{display:title == '搜索'?'flex':'none'}" />
      <HomeSort v-bind:style="{display:title == '分类'?'flex':'none'}"/>
      <HomeMore v-bind:style="{display:title == '更多'?'flex':'none'}"/>
    </section>
    <footer>
      <a @click="changeHome('搜索')">搜索</a>
      <a @click="changeHome('分类')">分类</a>
      <a @click="changeHome('更多')">更多</a>
    </footer>
  </div>
</template>

<script>
  import HomeSearch from './HomeSearch'
  import HomeSort from './HomeSort'
  import HomeMore from './HomeMore'
export default {
  name: 'Home',
  data () {
    return {
      title: '搜索'
    }
  },
  methods: {
    changeHome: function (key) {
      this.title = key
    }
  },
  components: {
    HomeSearch,
    HomeSort,
    HomeMore
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.home{
  height: 100%;
  display: flex;
  flex-direction: column;
}
  header{
    height: 50px;
    background-color: #fff;
    border-bottom: 1px solid #eee;
    text-align: center;
    line-height: 50px;
    color:#29B4F0;
    font-size: 20px;
  }
  section{
    display: flex;
    flex-grow: 1;
    flex-basis: 0%;
    overflow: auto;
  }
  section>*{
    display: flex;
    flex-grow: 1;
    display: none;
  }
  footer{
    height: 50px;
    line-height: 50px;
    background-color: #fff;
    border-top: 1px solid #eee;
    display: flex;
    flex-direction: row;
    /*align-items: center;*/
    /*justify-content: space-around;*/
  }
  footer>*{
    flex-grow: 1;
    text-align: center;
    color: darkgrey;
  }
  footer>*:hover{
    text-decoration: none;
    color: #29B4F0;
  }
</style>



