<template>
  <div class="loading" v-show="show.show">
    <i class="fa fa-spinner fa-pulse fa-5x "></i>
  </div>
</template>

<script>
  export default {
    name: 'Detail',
    props:['show'],
    data () {
      return {

      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .loading{
    background-color:transparent;
    width: 100%;
    height:100%;
    line-height: 100%;
    position:absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }

</style>



